footer {
	width: 100%;
	border-top: 1px solid #bbb;
	padding: 100px 0;
	background: transparent;
	position: relative;
	> .inner {
		width: 1180px;
		margin: 0 auto;
		position: relative;

		.members {
			img {
				width: 50px;
			}
		}
	}

	p {
		text-align: center;
		font: 12px/1 'arial';
		color: #999;
		letter-spacing: 1px;
		text-align: center;
	}
}

@media screen and (max-width: $tablet) {
	footer {
		.inner {
			width: 100%;
			padding: 0px 40px;
		}
	}
}
